<template>
  <section class="main">
    <loader v-if="!loaded" />
    <aside v-if="loaded" class="sidebar">
      <div class="sidebar__my">
        <div class="header">
          <template v-if="isDeviceLinked">
            <span class="header__active">{{ $t("ACTIVE_DEVICE") }}</span>
            <img class="header__img" :src="currentDevice.icon" />
            <div class="header__desc">
              <span class="header__title">{{ currentDevice.model }} <span v-if="currentDevice.name">{{
                currentDevice.name }}</span></span>
              <span class="header__device">{{ currentDevice.ios }}</span>
              <ul class="header__list">
                <template v-if="currentDevice.plus">
                  <li class="header__divider"></li>
                  <li class="header__status header__status_pro">Plus</li>
                </template>
              </ul>
            </div>
          </template>

          <template v-else>
            <img class="header__img" src="@/assets/icon-nodevice.svg" />
            <div class="header__desc">
              <span class="header__error-status g-status g-status_error">{{
                $t("NO_DEVICE_LINKED")
              }}</span>
              <router-link to="/link" class="g-button">{{
                $t("LINK_YOUR_DEVICE")
              }}</router-link>
            </div>
          </template>
        </div>
        <nav>
          <ul class="nav" :class="{ nav_disabled: !isDeviceLinked }">
            <li class="nav__item" v-for="item in menuItems" :key="item.title">
              <a v-if="item.external" class="nav__link" :href="item.link" :target="item.target">
                {{ item.title }}
              </a>
              <router-link v-else class="nav__link" :to="item.link">{{
                item.title
              }}</router-link>
            </li>
          </ul>
        </nav>
      </div>
    </aside>

    <div v-if="loaded" class="content">
      <h1 class="intro">{{ $t("ENABLE_JIT_TITLE") }}</h1>
      <div class="row">
        <div class="content-inner">
          <p class="g-text g-text_justify">{{ $t("ENABLE_JIT_INTRO") }}</p>

          <template v-if="isDeviceLinked">
            <ol class="steps">
              <li class="steps__step">
                <img class="steps__icon" src="https://a.appdb.to/appdb_jitpair_releases/icon.png" />
                <html-with-links
                  :html="$t('ENABLE_JIT_STEP_1', { href_jitpair: '/appdb-jitpair', href_configure: '/my/configure' })" />
              </li>
              <li class="steps__step">
                <img class="steps__icon" src="@/assets/shortcuts-app.png" />
                <html-with-links
                  :html="$t('ENABLE_JIT_STEP_2', { href: 'https://www.icloud.com/shortcuts/e1f8b3caad4b4fa7b4d8dc56262a4ad0' })" />
              </li>
            </ol>
          </template>

          <template v-else>
            <div class="no-device-container">
              <img class="no-device-img" src="@/assets/icon-nodevice.svg" />
              <div class="no-device-desc">
                <span class="no-device-error g-status g-status_error">{{ $t("NO_DEVICE_LINKED") }}</span>
                <router-link to="/link" class="g-button">{{ $t("LINK_YOUR_DEVICE") }}</router-link>
              </div>
            </div>
          </template>
        </div>
      </div>

      <!-- JIT Capability Check -->
      <div v-if="isDeviceLinked" class="jit-check-container">
        <important-message v-if="currentDevice.can_use_jit === 'yes'" type="info">
          <div v-html="$t('CURRENT_DEVICE_CAN_USE_JIT_NOTICE', { href: '/my/installation-history' })"></div>
        </important-message>

        <important-message v-else-if="currentDevice.can_use_jit === 'no'" type="notice">
          <div v-html="currentDevice.can_use_jit_validation_error.translated"></div>
        </important-message>

        <div class="button-container" v-if="currentDevice.can_use_jit !== 'yes'">
          <button class="g-button jit-check-button" @click="checkJITCapability">
            {{ $t("CHECK_AGAIN") }}
          </button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import Loader from '@/components/Loader';
import getMainMenuItems from '@/utils/MainMenu';
import ImportantMessage from '@/components/ImportantMessage.vue';

export default {
  components: {
    HtmlWithLinks,
    Loader,
    ImportantMessage
  },
  data() {
    return {
      menuItems: []
    };
  },
  computed: {
    title() {
      return this.$t("ENABLE_JIT_TITLE");
    },
    loaded() {
      const { configLoaded, devicesLoaded } = this.$store.getters;
      return configLoaded && devicesLoaded;
    },
    isDeviceLinked() {
      return this.$store.getters.isDeviceLinked;
    },
    currentDevice() {
      return this.$store.getters.devices
        .find(device => device.lt === this.$store.getters.lt) || {};
    }
  },
  methods: {
    checkJITCapability() {
      Promise.all([
        this.$store.dispatch('getDevices'),
        this.$store.dispatch('getConfig')
      ]);
    }
  },
  created() {
    this.menuItems = getMainMenuItems();
  }
};
</script>

